@import 'variables';

.dashContainer{
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
  width: 100%;
  height: 100vh;

  @media(max-width: 1040px){
    grid-template-columns: 1fr;
  }
  @media(max-width: 780px){
    grid-template-columns: 1fr;
  }
  
  .sidebar{
    @include flexbox(column, flex-start, center);
    background-color: rgb(37, 37, 37);
    padding: 1.25rem 0;
    transition: transform 0.3s ease-in-out;
    position: relative;
    position: fixed;
    width: 200px;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 100;

    @media (max-width: 1040px) {
      width: 60px;
    }

    .sideLogo{
      width: 100%;
      padding: 0 1rem;
      @include flexbox(row, space-between, center);

      span{
        font-size: 32px;
        font-weight: 700;

        @media(max-width: 1040px){
          display: none;
          font-size: 30px;
        }
        @media(max-width: 780px){
          font-size: 28px;
        }
        @media(max-width: 600px){
          font-size: 26px;
        }
      }
      img{
        display: none;

        @media(max-width: 1040px){
          display: block;
          width: 24px;
        }
      }
      .hidden{
        display: none;
      }
    }

    .userLogo{
      color: rgb(160, 243, 211);
    }
    .bizLogo{
      color: rgb(178, 163, 247);
    }
    .adminLogo{
      color: rgb(247, 239, 163);
    }


    .pageLinks{
      @include flexbox(column, flex-start, center);
      margin-top: 2.5rem;
      width: 100%;
      height: 100%;

      button{
        @include flexbox(row, start, center);
        width: 100%;
        margin-bottom: 0;
        padding: 1.05rem 1rem;
        transition: .2s ease;
        background: transparent;
        border: none;

        @media(max-width: 780px){
          padding: .85rem 1rem;
        }

        img{
          width: 24px;
          opacity: .75;

          @media(max-width: 780px){
            width: 22px;
          }
          @media(max-width: 600px){
            width: 20px;
          }
        }

        span{
          color: rgb(236, 234, 234);
          font-size: 14px;
          font-weight: 500;
          margin-left: 1rem;

          @media(max-width: 1040px){
            display: none;
          }
          @media(max-width: 780px){
            font-size: 12px;
          }
        }

        &:hover{
          background-color: rgb(63, 62, 62);
          cursor: pointer;

          img{
            opacity: 1;
          }
        }
      }

      button:last-child {
        margin-top: auto;
      }
    }

    .userPages{
      button:hover{ 
        span{
          color: rgb(80, 245, 182);
        }
      }
    }

    .bizPages{
      button:hover{
        span{
          color: rgb(160, 126, 253);
        }
      }
    }

    .adminPages{
      button:hover{
        span{
          color: rgb(250, 248, 111);
        }
      }
    }
  }

  .dashBody{
    padding-left: 200px;
    margin-left: 0;

    @media(max-width: 1040px){
      padding-left: 60px;
    }

    .dashHead{
      @include flexbox(row, space-between, center);
      padding: .8rem 0;
      border-bottom: 2px solid rgb(233, 231, 231);
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      padding-left: 200px;
      background-color: #f4f4f4;
      z-index: 10;

      @media(max-width: 1040px){
        padding-left: 60px;
      }

      .currentPage{
        padding: 0 1.65rem;
        font-size: 1.35rem;
        font-weight: 700;
        color:rgb(53, 50, 50);
      }

      .profileBox{
        padding: 0 1.65rem;
        @include flexbox(row, center, center);

        img{
          width: 40px;
        }
        span{
          font-weight: 600;
          font-size: 1.15rem;
          margin-left: .55rem;
        }
      }

      .userProfileBox{
        span{
          color: rgb(18, 51, 18);
        }
      }

      .bizProfileBox{
        span{
          color: rgb(38, 13, 77);
          margin-top: 4px;
        }
      }
      .adminProfileBox{
        span{
          color: rgb(82, 74, 5);
          margin-top: 4px;
        }
      }
    }

    .dashMain{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      padding: 5rem 1.5rem .5rem 1.5rem;
      overflow-y: auto;

      @media(max-width: 600px){
        padding: 5rem .8rem .5rem .8rem;
      }
      
      .hidden{
        display: none;
      }
    }
  }
}

.dashContainer.active{
  .sidebar{
    width: 180px;

    .sideLogo{
      span{
        display: block;
      }
    }
  }

  .pageLinks{
    button{
      span{
        display: block;
      }
    }
  }
}


.banner {
  background-color: #4CAF50;
  color: white;
  text-align: center;
  padding: 50px 20px;
}

.banner h1 {
  font-size: 36px;
  margin-bottom: 10px;
}

.banner p {
  font-size: 18px;
}

.tips h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #4CAF50;
}

p {
  margin-bottom: 20px;
  font-size: 16px;
  color: #555;
}

.tips_section {
  display: flex;
  justify-content: center;
}

.tip {
  background-color: white;
  padding: 20px;
  margin: 10px;
  width: 30%;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  text-align: center;
}

.tip h3 {
  color: #333;
  margin-bottom: 10px;
}

.tip p {
  color: #777;
}

.videos {
  text-align: center;
}

.video {
  margin: 20px 0;
}

.video h3 {
  margin-bottom: 10px;
}

.video iframe {
  width: 100%;
  height: 400px;
  max-width: 600px;
}

.stories {
  text-align: center;
  margin-top: 50px;
}

.notifications-container {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 5px;
}

.notifications-container h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.notifications-container ul {
  list-style-type: none;
  padding: 0;
}

.notifications-container li {
  padding: 10px;
  margin-bottom: 5px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.notifications-container li.unread {
  font-weight: bold;
}

.container.support {
  background-color: #f9f9f9;
  padding: 20px;
  margin-top: 20px;
  width: 100%;
  max-width: 800px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  text-align: center;

  h1 {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: center;

    div {
      margin-bottom: 20px;
      width: 100%;
      max-width: 600px;

      label {
        display: block;
        font-size: 16px;
        margin-bottom: 5px;
        color: #555;
        text-align: left;
      }

      input[type="text"],
      textarea {
        width: 100%;
        padding: 10px;
        font-size: 16px;
        border: 1px solid #ddd;
        border-radius: 5px;
      }

      textarea {
        height: 150px;
        resize: none;
      }
    }

    button {
      background-color: #4CAF50;
      color: white;
      padding: 10px 20px;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      cursor: pointer;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: #45A049;
      }
    }
  }

  #supportMessage {
    margin-top: 20px;
    font-size: 16px;
    color: #333;
  }
}

.container.leaderboard {
  background-color: #f9f9f9;
  padding: 20px;
  margin-top: 20px;
  width: 100%;
  max-width: 800px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  text-align: center;

  h1 {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
  }

  ul {
    list-style-type: none;
    padding: 0;

    li {
      padding: 10px;
      margin-bottom: 10px;
      background-color: #fff;
      border: 1px solid #ddd;
      border-radius: 5px;
      font-size: 16px;
      color: #555;
      display: flex;
      justify-content: space-between;

      span {
        font-weight: 600;
        color: #4CAF50;
      }
    }
  }
}

