.profileContainer {
  padding: 2rem;
  background-color: #f9f9f9;
  max-width: 600px;
  margin: 0 auto;

  h1 {
    text-align: center;
    margin-bottom: 1.5rem;
  }

  .bizProfileForm {
    display: flex;
    flex-direction: column;

    .formGroup {
      margin-bottom: 1rem;

      label {
        font-weight: bold;
        margin-bottom: 0.5rem;
        display: block;
      }

      input {
        width: 100%;
        padding: 0.75rem;
        border: 1px solid #ccc;
        border-radius: 4px;
      }
    }

    .saveBtn {
      padding: 0.75rem;
      background-color: #4CAF50;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      transition: background-color 0.3s;

      &:hover {
        background-color: #45a049;
      }
    }
  }
}
